<template>
  <div>
    <div class="input-suffix-detail">
      <div>
        <el-input placeholder="按标题或主键搜索" v-model="inputArticle" size="small">
          <i slot="suffix" class="el-input__icon el-icon-search"></i>
        </el-input>
        <el-button @click="ToFirstAnalyse" type="primary" plain size="small">初步分析</el-button>
        <router-link to="SecondAnalyse">
          <el-button type="success" plain size="small">二次分析</el-button>
        </router-link>
        <el-button @click="ToOutputH5" type="info" plain size="small">输出结果</el-button>
      </div>
    </div>
    <div>
      <div class="articleSelf">
        <div>
          <h5>标题：</h5>
          {{ articleTit }}
        </div>
        <div>
          <h5>摘要：</h5>
          {{ artSummary }}
        </div>
        <div>
          <h5>文章标签：</h5>
          {{ all_tags }}
        </div>
        <div>
          <h5>文章作者：</h5>
          {{ article_author }}&nbsp;&nbsp;&nbsp;
          <h5>分类：</h5>
          {{ category }}&nbsp;&nbsp;&nbsp;
          <h5>关键词：</h5>
          {{ source_key_words }}&nbsp;&nbsp;&nbsp;
          <h5>来源：</h5>
          {{ artSource }}
        </div>
        <div>
          <h5>文章类型(0 图文；1 纯文；2 纯图)：</h5>
          {{ atype }}
        </div>
        <div>
          <h5>浏览量：</h5>
          {{ view_count }}&nbsp;&nbsp;&nbsp;
          <h5>评论量：</h5>
          {{ comment_count }}&nbsp;&nbsp;&nbsp;
          <h5>字数：</h5>
          {{ word_num }}&nbsp;&nbsp;&nbsp;
          <h5>文章内图片数量：</h5>
          {{ article_img_num }}
        </div>
        <div>
          <h5>是否有外部链接：</h5>
          {{ has_extern_link }}
        </div>
        <div>
          <h5>文章url：</h5>
          {{ link }}
        </div>
        <div>
          <h5>状态码：</h5>
          {{ status }}&nbsp;&nbsp;&nbsp;
          <h5>id：</h5>
          {{ id }}
        </div>
        <div>
          <h5>标题md5：</h5>
          {{ article_title_md5 }}
          <h5>文章内容md5：</h5>
          {{ content_md5 }}
        </div>
        <div>
          <h5>操作时间：</h5>
          {{ operate_time }} &nbsp;&nbsp;&nbsp;
          <h5>操作人：</h5>
          {{ operator }}
        </div>
        <div>
          <h5>文章爬取时间：</h5>
          {{ download_time }}&nbsp;&nbsp;&nbsp;
          <h5>爬取人：</h5>
          {{ username }}
        </div>
        <div>
          <h5>文章写作时间：</h5>
          {{ publish_time }}&nbsp;&nbsp;&nbsp;
          <h5>入库时间：</h5>
          {{ ctime }}
        </div>
        <div>
          <h5>文章内图片：</h5>
          <!-- <div class="art-img"  v-for="item in article_img">
                  <img :src="item">
          </div>-->
          {{ article_img }}
        </div>
        <div>
          <h5>封面图：</h5>
          {{ list_img }}
        </div>
        
        <div>
          <h5>原文内容的资源id（原始HTML）：</h5>
          {{ content }}
        </div>
        <!-- #原始html 文章详情-->
        <div>
          <h5>文章转json后的资源id(初步分析)：</h5>
          {{ json_content }}
        </div>
        <!--# 第一步转换json数据 初步分析-->
        <div>
          <h5>文章分析后的资源id（二次分析）：</h5>
          {{ analyze_content }}
        </div>
        <!--# 第二步文章分析json数据 二次分析-->
        <div>
          <h5>文章转H5后的资源id（输出H5）：</h5>
          {{ html_content }}
        </div>
        <!-- #重构的html 输出详情-->
        
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      inputArticle: "",
      articleId: this.$route.params.articleId,
      articleAllInfo: "",
      articleTit: "",
      artSource: "",
      artSummary: "",
      all_tags: "",
      source_key_words: "",
      articleStatus: "",
      articleTag: "",
      has_extern_link: "",
      download_time: "",
      ctime: "",
      operate_time: "",
      operator: "",
      view_count: "",
      score: "",
      status: "",
      article_author: "",
      comment_count: "",
      word_num: "",
      publish_time: "",
      list_img: "",
      article_title_md5: "",
      content_md5: "",
      username: "",
      link: "",
      json_content: "",
      content: "",
      id: "",
      category: "",
      article_img_num: "",
      article_img: "",
      atype: "",
      html_content: "", //h5
      analyze_content: ""
    };
  },
  mounted() {
    this.getArticleAllInfo();
  },
  methods: {
    getArticleAllInfo() {
      let _this = this;
      _this.articleId = window.localStorage.getItem("articleId");
      axios
        .post(
          "/article/art_detail/",
          qs.stringify({
            art_id: _this.articleId
          })
        )

        .then(function(resData) {
          let res = resData.data.resultObj;
          res.forEach(element => {
            _this.articleAllInfo = element;
            for (let attr in element) {
              _this.articleTit = element.title;
              _this.word_num = element.word_num;
              _this.artSource = element._source;
              _this.article_author = element.article_author;
              _this.all_tags = element.all_tags;
              _this.score = element.score;
              _this.view_count = element.view_count;
              _this.download_time = element.download_time;
              _this.has_extern_link = element.has_extern_link;
              _this.operate_time = element.operate_time;
              _this.comment_count = element.comment_count;
              _this.status = element.status;
              _this.publish_time = element.publish_time;
              _this.list_img = element.list_img;
              _this.article_title_md5 = element.article_title_md5;

              if (element.content_md5 == null) {
                _this.content_md5 = "NULL";
              }
              _this.artSummary = element._summary;
              _this.source_key_words = element.source_key_words;
              _this.username = element.username;
              _this.link = element.link;
              _this.json_content = element.json_content;
              _this.operator = element.operator;
              _this.id = element.id;
              _this.category = element.category;
              _this.article_img_num = element.article_img_num;
              _this.article_img = element.article_img;
              _this.ctime = element.ctime;
              _this.atype = element.atype;
              _this.content = element.content;
              _this.atype = element.atype;
              if(element.analyze_content == null){
                 _this.analyze_content = 'null';
              }else{
                _this.analyze_content = element.analyze_content;
              }
              if(element.html_content == null){
                 _this.html_content = 'null';
              }else{
                _this.html_content = element.html_content;
              }
              
            }
          });
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
    ToFirstAnalyse() {
      window.localStorage.setItem("content", this.content);
      window.localStorage.setItem("firAnalyse", this.json_content);
      window.localStorage.setItem("outPutContent", this.html_content);
      window.localStorage.setItem("articleId", this.id);
      this.$router.push({
        name: "FirstAnalyse",
        params: {
          firAnalyse: this.json_content
        }
      });
    },
    ToOutputH5() {
      window.localStorage.setItem("content", this.content);
      window.localStorage.setItem("outPutContent", this.html_content);
      this.$router.push({
        name: "OutputArticle",
        params: {
          outPutContent: this.html_content
        }
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
.input-suffix-detail div {
  margin: 5px 3px;
  width: 50%;
}

h5 {
  display: inline-block;
  color: black;
  margin: 3px 5px;
  padding: 0;
}

div {
  color: #545252;
}

.articleSelf {
  display: flex;
  flex-direction: column;

  .art-img {
    display: flex;
    flex-direction: row;
  }
}
</style>

