<template>
    <div>
        <div class="input-suffix">
            <div>
                <el-input
                    placeholder="按标题或主键搜索"
                    v-model="inputArticle"
                    size="small"
                    >
                    <i slot="suffix" class="el-input__icon el-icon-search"></i>
                </el-input>
                 <router-link to="FirstAnalyse"><el-button type="primary" plain size="small">初步分析</el-button></router-link> 
                 <router-link to="SecondAnalyse"><el-button type="success" plain size="small">二次分析</el-button></router-link>
                <el-button type="info" plain size="small">输出结果</el-button>
            </div>
         </div>
         <div>
             <div v-html="articleSelf"></div>
             <!-- <iframe :src="articleSelf" frameborder="0"></iframe> -->
         </div>
    </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      inputArticle: "",
      contentId:this.$route.params.content,
      articleSelf:''
    };
  },
  mounted() {
    this.getArticleContent();
  },
  methods:{
    getArticleContent(){
      let _this = this;
      let contentId = window.localStorage.getItem('content')
      _this.contentId = contentId
      console.log(contentId)
      axios
        .post('/article/art_content/', qs.stringify({
          text_id: _this.contentId
        }))
          
        .then(function(resData) {
          let res = resData.data.resultObj;
          _this.articleSelf = res
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
      
    },
  }
  
}
</script>

<style lang="stylus" scoped>
.input-suffix div
  // margin 5px
  width 50%
    
</style>

