<template>
  <div>
    <div class="input-suffix">
      <div>
        <el-input placeholder="按标题或主键搜索" v-model="inputArticle" size="small">
          <i slot="suffix" class="el-input__icon el-icon-search"></i>
        </el-input>
        <el-button @click="ToFirstAnalyse" type="primary" plain size="small">初步分析</el-button>
        <el-button @click="ToArticleAllInfo" type="success" plain size="small">文章详情</el-button>
        <el-button @click="ToOutputH5" type="info" plain size="small">输出结果</el-button>
      </div>
    </div>
    <div>
      <ul>
        <li>
          <h4>文章ID：</h4>
          {{articleId}}
        </li>
        <li>
          <h4>标题：</h4>
          {{artTitle}}
        </li>
        <li>
          <h4>原始关键词：</h4>
          {{originKeys}}
        </li>
        <li>
          <h4>分析关键词：</h4>
          {{analyze_keys}}
        </li>
        <li>
          <h4>新词：</h4>
          {{new_words}}
        </li>
        <li>
          <h4>原始链接：</h4>
          {{originLink}}
        </li>
      </ul>
    </div>
    <div>
      <div v-for="(key, val, index) in artSecondContent" class="sec-para">
        <div>
          <h5>段落：</h5>
          {{key.name}}
        </div>
        <div>
          <h5>标题：</h5>
          {{key.title}}
        </div>
        <div>
          <h5>段落类型：</h5>
          {{key.html_class}}
        </div>
        <div>
          <h5>段落：</h5>
          {{key.sentences}}
        </div>
         <div v-for="(item,i) in key.cut_sent">
          <div><h5>句子id：</h5><span>{{item.id}}</span> </div>
          <div><h5>句子切分：</h5><span>{{item.mr}}</span></div> 
         </div>
         <!-- <div>
           <h5>段落分析：</h5>{{ key.match_analysis_result}}
         </div> -->
      </div>
      
    </div>
  </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      inputArticle: "",
      secondAnalyseId: this.$route.params.content,
      artTitle: "",
      artSecondContent: "",
      articleId: "",
      contentId: "",
      firAnalyse: "",
      outPutContent: "",
      originKeys: "",
      analyze_keys: "",
      new_words: "",
      originLink: "",
      cut_sent_id: "",
      cut_sent_mr: "",
      cutAll: "",
      acut_sent: ""
    };
  },
  mounted() {
    this.getLocalSave();
    this.getSecondTitle();
    this.getSecondContent();
  },
  methods: {
    getLocalSave() {
      this.secondAnalyseId = window.localStorage.getItem("secondAnalyseId");
      this.articleId = window.localStorage.getItem("articleId");
      this.contentId = window.localStorage.getItem("content");
      this.firAnalyse = window.localStorage.getItem("firAnalyse");
      this.outPutContent = window.localStorage.getItem("outPutContent");
    },
    getSecondTitle() {
      let _this = this;
      if(this.secondAnalyseId == 'null'){
        _this.$message({
          type:'error',
          message:'文章二次分析资源id为NULL',
          showClose:'true'
        })
      }
      axios
        .post(
          "/article/analyze_page_head/",
          qs.stringify({
            text_id: _this.secondAnalyseId
          })
        )
        .then(function(resData) {
          let res = resData.data.resultObj;
          _this.artTitle = res.title;
          _this.originKeys = res.original_keys;
          _this.analyze_keys = res.analyze_keys;
          _this.new_words = res.new_words;
          _this.originLink = res.link;
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
    getSecondContent() {
      let _this = this;
      axios
        .post(
          "/article/analyze_paragraph/",
          qs.stringify({
            text_id: _this.secondAnalyseId
          })
        )
        .then(function(resData) {
          let res = resData.data.resultObj;
          let arrObjSec = res.resultObj.paragraph;
          _this.artSecondContent = arrObjSec;
          let arrObjSecondP = [];
          for (let i = 0; i < arrObjSec.length; i++) {
            if (arrObjSec[i].cut_sent !== undefined) {
              let acut_sent = arrObjSec[i].cut_sent;
              _this.acut_sent = acut_sent;
              console.log(acut_sent)
            }
          }
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
    ToFirstAnalyse() {
      window.localStorage.setItem("content", this.contentId); //头部，原始
      window.localStorage.setItem("firAnalyse", this.firAnalyse); //
      window.localStorage.setItem("articleId", this.articleId); //详情
      this.$router.push({
        name: "FirstAnalyse",
        params: {
        }
      });
    },
    ToArticleAllInfo(){
      this.articleId = window.localStorage.getItem('articleId')
      window.localStorage.setItem('articleId', this.articleId)
      this.$router.push({
            name: "ArticleAllInfo",
            params: {
              articleId: this.articleId
            }
          });
    },
    ToOutputH5(){
      window.localStorage.setItem('content', this.contentId);
      window.localStorage.setItem('outPutContent', this.outPutContent);
          this.$router.push({
            name: "OutputArticle",
            params: {
              outPutContent: this.outPutContent,
              content: this.contentId
            }
          });
    }
  }
};
</script>
<style lang="stylus" scoped>
ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
  margin-right: 10px;
  padding: 0;
  border: 1px solid #DADADA;
  font-size: 16px;
  line-height: 24px;
}

h4, h5 {
  display: inline-block;
  margin: 3px 5px;
  padding: 0;
  line-height: 24px;
}

.input-suffix div {
  width: 50%;
}

.sec-para {
  border: 1px solid #5c5c5c;
  padding 10px 0
}

.sec-para:nth-child(odd){
      background #9e659d30
    }

</style>