<template>
  <div>
    <div class="input-suffix-output">
      <div>
        <el-input placeholder="按标题或主键搜索" v-model="inputArticle" size="small">
          <i slot="suffix" class="el-input__icon el-icon-search"></i>
        </el-input>
        <el-button @click="ToFirstAnalyse" type="primary" plain size="small">初步分析</el-button>
        <!-- <el-button @click="ToArticleSecond" type="success" plain size="small">二次分析</el-button> -->
      </div>
    </div>
    <div class="article-fir-tit">
      <div>
        <h4>标题：</h4>
        {{articleFirTit}}
      </div>
      <div>
        <h4>原始关键词：</h4>
        {{articleFirTag}}
      </div>
      <div>
        <ul>
          <li>
            <h4>文章ID：</h4>
            {{articleId}}
          </li>
          <li>
            <h4>来源：</h4>
            {{artSource}}
          </li>
          <li>
            <h4>作者：</h4>
            {{article_author}}
          </li>
          <li>
            <h4>写作时间：</h4>
            {{publish_time}}
          </li>
        </ul>
      </div>
      <div>
        <ul>
          <li>
            <h4>总字数：</h4>
            {{articleFirWords}}
          </li>
          <li>
            <h4>图片数：</h4>
            {{articleFirImgs}}
          </li>
          <li>
            <h4>浏览量数：</h4>
            {{articleFirViewCount}}
          </li>
        </ul>
      </div>
      <div>
        <h4>原始链接：</h4>
        {{originLink}}
      </div>
      
    </div>
    <div class="art-attr">
      <div class="init-article" v-html="articleSelf"></div>
      <div class="firAna-article" v-html="articleOutput"></div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      inputArticle: "",
      outputContentId: this.$route.params.outPutContent,
      articleOutput: "",
      contentId: "", //原文id
      articleSelf: "",
      articleId: "", //详情id
      firAnalyse: this.$route.params.firAnalyse,
      articleFirTit: "",
      articleFirTag: "",
      articleFirWords: "",
      articleFirParL: "",
      articleFirViewCount: "",
      articleFirImgs: "",
      articleFirPar: "",
      artSource: "",
      publish_time: "",
      article_author: "",
      originLink: ""
    };
  },
  mounted() {
    this.getLocalResourceId();
    this.getArticleTitle();
    this.getArticleContent();
    this.getInitArticleContent();
  },
  methods: {
    getLocalResourceId() {
      this.contentId = window.localStorage.getItem("content");
      this.firAnalyse = window.localStorage.getItem("firAnalyse");
      this.articleId = window.localStorage.getItem("articleId");
    },
    getArticleTitle() {
      //头部信息+初步段落信息
      let _this = this;
      _this.firAnalyse = window.localStorage.getItem("firAnalyse");
      _this.articleId = window.localStorage.getItem("articleId");
      axios
        .post(
          "/article/art_content/",
          qs.stringify({
            text_id: _this.firAnalyse
          })
        )

        .then(function(resData) {
          let res = resData.data.resultObj;
          let artInfo = res.articleInfo; //基本信息
          _this.articleFirTit = artInfo.title;
          _this.artSource = artInfo._source;
          _this.article_author = artInfo.article_author;
          _this.publish_time = artInfo.publish_time;
          _this.articleFirTag = artInfo.source_key_words;
          _this.articleFirWords = artInfo.word_num;
          _this.originLink = artInfo.link;
          _this.articleFirImgs = artInfo.article_img_num;
          _this.articleFirViewCount = artInfo.view_count;
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
    getInitArticleContent() {
      //原文
      let _this = this;
      let contentId = window.localStorage.getItem("content");
      _this.contentId = contentId;
      axios
        .post(
          "/article/art_content/",
          qs.stringify({
            text_id: _this.contentId
          })
        )

        .then(function(resData) {
          let res = resData.data.resultObj;
          _this.articleSelf = res;
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
    getArticleContent() {
      //转h5文章
      let _this = this;
      _this.outputContentId = window.localStorage.getItem("outPutContent");
     if(this.outputContentId == 'null'){
        _this.$message({
          type:'error',
          message:'输出H5资源id为NULL',
          showClose:'true'
        })
      }
      axios
        .post(
          "/article/art_content/",
          qs.stringify({
            text_id: _this.outputContentId
          })
        )

        .then(function(resData) {
          let res = resData.data.resultObj;
          _this.articleOutput = res;
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },

    ToArticleAllInfo() {
      this.articleId = window.localStorage.setItem("articleId", this.articleId);
      this.$router.push({
        name: "ArticleAllInfo",
        params: {
          articleId: this.articleId
        }
      });
    },

    ToFirstAnalyse() {
      window.localStorage.setItem("content", this.contentId);
      window.localStorage.setItem("firAnalyse", this.firAnalyse);
      window.localStorage.setItem("articleId", this.articleId);
      this.$router.push({
        name: "FirstAnalyse",
        params: {
          // firAnalyse: this.firAnalyse,
          // content: this.contentId
        }
      });
    },
    ToArticleSecond(){
      this.articleId = window.localStorage.getItem('articleId')
      this.secondAnalyseId = window.localStorage.getItem('secondAnalyseId')
      this.secondAnalyseId = window.localStorage.getItem('outPutContent')
      window.localStorage.setItem('articleId', this.articleId)
      window.localStorage.setItem('content', this.contentId)
      window.localStorage.setItem('firAnalyse', this.firAnalyse)
      window.localStorage.setItem('secondAnalyseId', this.secondAnalyseId )
      window.localStorage.setItem('outPutContent', this.outPutContent )
      this.$router.push({
            name: "SecondAnalyse",
            params: {
              articleId: this.articleId,
              secondAnalyseId: this.secondAnalyseId,
            }
          });
    },
  }
};
</script>

<style lang="stylus" scope>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
  display: inline-block;
  margin-right: 10px;
  padding: 0;
}

h4 {
  display: inline-block;
  margin: 3px 5px;
  padding: 0;
}

.input-suffix-output div {
  margin: 5px;
  width: 50%;
}

.article-fir-tit {
  // background: #C9BBC8;
  padding 10px 5px
}

.art-attr {
  display: flex;
  flex-direction: row;
  justify-content center
  align-content center
  flex-grow: 1;
  width: 100%;

  /* text-indent: 2em */
}
img {
  width: 375px !important;
  inline-size: 375px !important;
}
.init-article {
  display: flex;
  flex-direction: column;
  width: 375px;
  background: #C5E7F1;
  margin: 0 10px;
  padding: 5px 10px;
}
.firAna-article {
  display: flex;
  flex-direction: column;
  width: 375px;
  line-height: 24px;
  background: #E5C1CD;
  margin: 0 30px;
  padding: 5px 10px;
}
</style>

