<template>
    <div>
        <div class="input-suffix">
            <div class='top-search'>
                <el-input
                    placeholder="按标题或主键搜索"
                    v-model="inputArticle"
                    size="small">
                    <i slot="suffix" class="el-input__icon el-icon-search"></i>
                </el-input>
                <el-button @click="ToArticleAllInfo" type="primary" plain size="small">文章详情</el-button>
                 <router-link to="SecondAnalyse"><el-button type="success" plain size="small">二次分析</el-button></router-link>
                <el-button @click="ToOutputH5" type="info" plain size="small">输出结果</el-button>
            </div>
            <div>
              <div class="article-fir-tit">
                <div><h4>标题：</h4>{{articleFirTit}}</div>
                <div><h4>原始关键词：</h4>{{articleFirTag}}</div>
                <div>
                  <ul>
                    <li><h4>文章ID：</h4>{{articleId}}</li>
                    <li><h4>来源：</h4>{{artSource}}</li>
                    <li><h4>作者：</h4>{{article_author}}</li>
                    <li><h4>写作时间：</h4>{{publish_time}}</li>
                  </ul> 
                 </div>
                 <div>
                   <ul>
                     <li><h4>总字数：</h4>{{articleFirWords}}</li>
                     <li><h4>图片数：</h4>{{articleFirImgs}}</li>
                     <li><h4>浏览量数：</h4>{{articleFirViewCount}}</li>
                   </ul>
                 </div>
                 <div><h4>原始链接：</h4>{{originLink}}</div>
                
              </div>
              <div class="artAttr">
               <div class="init-article" v-html="articleSelf"></div>
               <div class="firAna-article" >
                 <div v-for="(key, val, index) in articleFirPar" class="paragraph">
                   <div><h4>  段落标题：</h4>{{key.title}}</div>
                   <!-- <div class="section">
                     <ul>
                      <li><h4>  段落类型：</h4>{{key.html_class}}</li>
                      <li><h4>  总字数：</h4>{{key.word_count}}</li>
                      <li><h4>  句子数：</h4>{{key.sent_count}}</li>
                      </ul>
                   </div> -->
                   <!-- <div><h4>段落信息：</h4>{{ key.sentences}}</div> -->
                   <div><h4>句子信息：</h4>
                   <span v-for="(key, index, val) in key.sentences">{{index}}，{{key}}</span>
                   </div>
                 </div>
               </div>
              </div>
              
            </div>
            
         </div>
    </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      inputArticle: "",
      firAnalyse:this.$route.params.firAnalyse,
      articleFirTit:'',
      articleId:'',
      contentId:'',
      articleFirTag:'',
      articleFirWords:'',
      articleFirParL:'',
      articleFirViewCount:'',
      articleFirImgs:'',
      articleFirPar:'',
      artSource:'',
      publish_time:'',
      article_author:'',
      originLink:'',
      articleSelf:'',
      outPutContent:'',
    };
  },
  mounted() {
    this.getLocalResourceId();
    this.getArticleContent();
    this.getInitArticleContent();
  },
  methods:{
    getLocalResourceId(){
      this.outPutContent = window.localStorage.getItem('outPutContent')
    },
    getInitArticleContent(){ //原文
      let _this = this;
      let contentId = window.localStorage.getItem('content')
      _this.contentId = contentId
      axios
        .post('/article/art_content/', qs.stringify({
          text_id: _this.contentId
        }))
          
        .then(function(resData) {
          let res = resData.data.resultObj;
          _this.articleSelf = res
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
      
    },
      getArticleContent() { //头部信息+初步段落信息
      let _this = this;
      _this.firAnalyse = window.localStorage.getItem('firAnalyse')
      _this.articleId = window.localStorage.getItem('articleId')
      axios
        .post('/article/art_content/', qs.stringify({
          text_id: _this.firAnalyse
        }))
          
        .then(function(resData) {
          let res = resData.data.resultObj;
          let artInfo = res.articleInfo //基本信息
          _this.articleFirTit = artInfo.title
          _this.artSource = artInfo._source
          _this.article_author = artInfo.article_author
          _this.publish_time = artInfo.publish_time
          _this.articleFirTag = artInfo.source_key_words
          _this.articleFirWords = artInfo.word_num
          _this.originLink = artInfo.link
          _this.articleFirImgs = artInfo.article_img_num
          _this.articleFirViewCount = artInfo.view_count
          _this.articleFirPar = res.paragraph //段落信息
          _this.articleFirParL = res.paragraph.length
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
    ToArticleAllInfo(){
      this.articleId = window.localStorage.getItem('articleId')
      window.localStorage.setItem('articleId', this.articleId)
      this.$router.push({
            name: "ArticleAllInfo",
            params: {
              articleId: this.articleId
            }
          });
    },
    ToArticleSecond(){
      this.articleId = window.localStorage.getItem('articleId')
      this.secondAnalyseId = window.localStorage.getItem('secondAnalyseId')
      this.secondAnalyseId = window.localStorage.getItem('outPutContent')
      window.localStorage.setItem('articleId', this.articleId)
      window.localStorage.setItem('content', this.contentId)
      window.localStorage.setItem('firAnalyse', this.firAnalyse)
      window.localStorage.setItem('secondAnalyseId', this.secondAnalyseId )
      window.localStorage.setItem('outPutContent', this.outPutContent )
      this.$router.push({
            name: "SecondAnalyse",
            params: {
              articleId: this.articleId,
              secondAnalyseId: this.secondAnalyseId,
            }
          });
    },
    ToOutputH5(){
      window.localStorage.setItem('content', this.contentId);
      window.localStorage.setItem('outPutContent', this.outPutContent);
          this.$router.push({
            name: "OutputArticle",
            params: {
              outPutContent: this.outPutContent,
              content: this.contentId
            }
          });
    }
  }
  
}
</script>
