  margin 5px
  width 50%
h4 
  display inline-block
  margin 3px 5px
  padding 0
div
 color #545252
.artAttr
  display flex
  flex-direction row
  height 100%
  width 100%
  line-height 24px
  .init-article
    display flex
    flex-direction column
    height 100%
    width 50%
    background #C5E7F1
  .firAna-article
    display flex
    flex-direction column
    height 100%
    width 50%
    background #E5C1CD
    .paragraph:nth-child(even){
      background #F3DBCF
    }
    .paragraph
      border-bottom 1px solid #000
    .section
      border-bottom 1px solid #ccc
</style>
