<template>
  <div class="about">
    <div class="input-suffix">
      
      <div>
        <el-select v-model="searchValue" filterable placeholder="请选择要查询类型" size="small">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div>
        <el-input placeholder="请输入id或标题" v-model="inputArticle" size="small" @keyup.enter.native="searchById">
          <i slot="suffix" class="el-input__icon el-icon-search"></i>
        </el-input>
      </div>&nbsp;
      <div>
        <el-date-picker
          v-model="startTimeValue3"
          type="date"
          placeholder="选择开始日期"
          format="yyyy 年 MM 月 dd 日 HH 时 mm 分"
          value-format="timestamp"
          style="width:100%"
          size="small"
        ></el-date-picker>
      </div>
      <div>
        <el-date-picker
          v-model="endtimeValue3"
          type="date"
          placeholder="选择截止日期"
          format="yyyy 年 MM 月 dd 日 HH 时 mm 分"
          value-format="timestamp"
          style="width:100%"
          size="small"
        ></el-date-picker>
      </div>
      
    </div>
    
    <div class="article-table">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[20, 50, 100, 300, 400,600]"
        :page-size="20"
        layout="total, sizes, prev, pager, next, jumper"
        :total="articleNum"
      ></el-pagination>
      <el-table
        :data.sync="tableData"
        border
        height="800"
        @cell-click="changeButton"
        style="width: 100%"
      >
        <el-table-column prop="id" label="id" width="90"></el-table-column>
        <el-table-column prop="title" label="标题" show-overflow-tooltip></el-table-column>
        <!-- <el-table-column prop="_summary" label="简介" show-overflow-tooltip></el-table-column> -->
        <el-table-column prop="_source" label="来源" width="100" show-overflow-tooltip></el-table-column>
        <!-- <el-table-column prop="source_key_words" label="原始标签" show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.source_key_words }}
          </template>
        </el-table-column>-->
        <el-table-column prop="category" label="分类" width="150"></el-table-column>
        <el-table-column prop="article_author" label="作者" width="120"></el-table-column>
        <el-table-column prop="status" label="状态" width="120"></el-table-column>
        <el-table-column prop="operator" label="操作人" width="120"></el-table-column>
        <el-table-column prop="operate_time" label="操作时间" width="150" show-overflow-tooltip></el-table-column>
        <el-table-column prop="ctime" label="入库时间" width="150" show-overflow-tooltip></el-table-column>
        <!-- <el-table-column prop="word_num" label="字数" width="80"></el-table-column> -->
        <!-- <el-table-column prop="article_img_num" label="图数" width="70"></el-table-column> -->
        <el-table-column label="文章" width="60">
          <template slot-scope="scope">
            <!--@click="handleClick(scope.row)"-->
            <el-button type="text" size="small">详情</el-button>
          </template>
        </el-table-column>
        <!-- <el-table-column label="原文" width="60">
          <template slot-scope="scope">
            <el-button type="text" size="small">预览</el-button>
          </template>
        </el-table-column>-->
        <el-table-column label="初步" width="60">
          <template slot-scope="scope">
            <el-button type="text" size="small">分析</el-button>
          </template>
        </el-table-column>
        <el-table-column label="二次" width="60">
          <template slot-scope="scope">
            <el-button type="text" size="small">分析</el-button>
          </template>
        </el-table-column>
        <el-table-column label="输出" width="60">
          <template slot-scope="scope">
            <el-button type="text" size="small">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <span class="status-means"><strong>状态码注释：</strong>  1：原文和转json后文章上传成功
300：分析文章后上传成功
301：分析文章失败
302：分析后上传资源框架失败
304：分析阶段从资源框架提取文章json数据失败
400：文章转h5后上传成功
401：文章转h5失败
402：文章转h5后上传资源框架失败
404：转h5阶段从资源框架提取文章json数据失败</span> 
    </div>
  </div>
</template>
<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      inputArticle: "",
      tableData: [],
      articleNum: 1,
      pageSize: 20,
      currentPage: 6,
      contentId: "",
      firAnalyse: "",
      secAnalyse: "",
      searchValue: "",
      options: [
        {
          value: "1",
          label: "标题"
        },
        {
          value: "2",
          label: "操作人"
        }
      ],
      startTimeValue3:'',
      endtimeValue3:'',

    };
  },
  // watch:{
  //   pageIndex: function () {
  //      if (this.pageIndex == 'null') {
  //         this.pageIndex = 1;
  //         window.localStorage.setItem('pageIndex', this.pageIndex);
  //       }
  //     }
     
  // },
  mounted() {
    this.currentPage = Number(window.localStorage.getItem('currentPage'));
    this.getArticleTable();
    this.localInformation();
  },
  methods: {
    // 点击表格中查看 合并 移动 删除 四个按钮
    changeButton(row, column, cell, event) {
      let tableLabel = column["label"];
      switch (tableLabel) {
        case "文章":
          window.localStorage.setItem("articleId", row["id"]);
          this.$router.push({
            name: "ArticleAllInfo",
            params: {
              articleId: row["id"]
            }
          });
          break;
        // case "原文":
        //   window.localStorage.setItem('content', row["content"]);
        //   this.$router.push({
        //     name: "ArticleDetail",
        //     params: {
        //       content: row["content"]
        //     }
        //   });
        //   break;
        case "初步":
          window.localStorage.setItem("content", row["content"]); //原始
          window.localStorage.setItem("firAnalyse", row["json_content"]); //初步
          window.localStorage.setItem("secondAnalyseId", row["analyze_content"]); //二次
          window.localStorage.setItem("outPutContent", row["html_content"]);
          window.localStorage.setItem("articleId", row["id"]);
          this.$router.push({
            name: "FirstAnalyse",
            params: {
              firAnalyse: row["json_content"],
              articleFirImgs: row["article_img"].length
            }
          });
          break;
        case "二次":
          window.localStorage.setItem("secondAnalyseId", row["analyze_content"]);
          window.localStorage.setItem("articleId", row["id"]);
          this.$router.push({
            name: "SecondAnalyse",
            params: {
              
            }
          });
          break;
        case "输出":
          window.localStorage.setItem("content", row["content"]);
          window.localStorage.setItem("firAnalyse", row["json_content"]);
          window.localStorage.setItem("outPutContent", row["html_content"]);
          window.localStorage.setItem("articleId", row["id"]);
          this.$router.push({
            name: "OutputArticle",
            params: {
              outPutContent: row["html_content"]
            }
          });
          break;
      }
    },
    getArticleTable() {
      var _this = this;
       
      axios
        .post(
          "/article/art_list/",
          qs.stringify({
            page_num: _this.currentPage, //
            per_num: _this.pageSize //
          })
        )
        .then(function(res) {
          if (res.data.code === 1) {
            let tableData = res.data.resultObj.records;
            _this.tableData = tableData;
            let tableDataNum = res.data.resultObj.total_num;
            _this.articleNum = tableDataNum;
          } else if (res.data.code == 2) {
            _this.$message({
              type: "info",
              message: "亲请再次刷新页面!"
            });
          }
        })
        .catch(function(error) {
          console.log("接口请求失败" + error.toString());
        });
    },
    //每页显示数量
    handleSizeChange(val) {
      this.pageSize = val;
      this.getArticleTable();
      console.log("数量"+ val);
    },
    //当前页：
    handleCurrentChange(val) {
      window.localStorage.setItem('currentPage', val);
      this.currentPage = val
      this.getArticleTable();
      console.log("当前页" + val);
    },
    // 本地存储
      localInformation() {
        window.localStorage.setItem('currentPage', this.currentPage);
        this.handleCurrentChange(this.currentPage)
        
      },
    searchById(){
      let _this = this;
      axios
        .post(
          "/article/art_detail/",
          qs.stringify({
            art_id: _this.inputArticle
          })
        )

        .then(function(resData) {
          // _this.tableData = resData
        })
        .catch(function(error) {
          console.log("概念详情接口请求失败" + error.toString());
        });
    },
  }
}
</script>

<style lang="stylus" scoped>
.input-suffix {
  width: 100%;
  display: flex;
  flex-direction: row;
}
.status-means{
  color #5c5c5c
}
</style>




